import React from 'react';
import { useTranslation } from 'react-i18next';
import { formatNumber } from '../../utils/functions';

import { ClockIcon, NewspaperIcon, PresentationChartLineIcon } from '@heroicons/react/24/solid';

const StatSection = ({ icon: Icon, title, value }) => {
  return (
    <div className="relative overflow-hidden rounded-lg bg-white px-4 pb-5 pt-5 shadow-xl sm:px-6 sm:pt-6">
      <dt>
        <div className="absolute rounded-md bg-rose-500 p-2.5">
          <Icon className="h-8 w-8 text-white" />
        </div>
        <p className="ml-20 rtl:ml-0 rtl:mr-20 truncate text-sm font-normal text-gray-600" dangerouslySetInnerHTML={{ __html: title }} />
      </dt>
      <dd className="ml-20 rtl:ml-0 rtl:mr-20 flex items-center">
        <p className="text-3xl font-extrabold text-gray-900">{formatNumber(value)}</p>
      </dd>
    </div>
  );
};

const Overview = ({ initialData }) => {
  const { t } = useTranslation();

  const sections = [
    {
      icon: PresentationChartLineIcon,
      title: t('Total Interactions To Date'),
      value: initialData.total,
    },
    {
      icon: NewspaperIcon,
      title: t('Today\'s Engagement Overview'),
      value: initialData.today
    },
    {
      icon: ClockIcon,
      title: t('Yesterday\'s Engagement Summary'),
      value: initialData.yesterday
    },
  ];

  return (
    <div>
      <dl className="mt-10 grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-3">
        {sections.map((section, index) => (
          <StatSection
            key={index}
            icon={section.icon}
            title={section.title}
            value={section.value}
            previousValue={section.previousValue}
          />
        ))}
      </dl>
    </div>
  );
};

export default Overview;