import React, { useState, useEffect } from 'react';
import Charts from '../../components/free/Charts';
import Items from '../../components/free/Items';
import Overview from '../../components/free/Overview';
import Metrics from '../../components/free/Metrics';
import Banners from '../../components/free/Banners';
import { isNotEmpty, capitalizeFirstLetter } from '../../utils/functions';
import { get } from '../../utils/services';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import NotFound from '../../components/common/NotFound';
import Loading from '../../components/common/Loading';

const Main = () => {
    const [error, setError] = useState(false);
    const [charts, setCharts] = useState(null);
    const [items, setItems] = useState(null);
    const [overview, setOverview] = useState(null);
    const [metrics, setMetrics] = useState(null);
    const navigate = useNavigate();
    const { t } = useTranslation();

    useEffect(() => {
      const fetchData = async () => {
        try {
          const response = await get('admin-ajax.php', { action: 'wp_ulike_stats_api' });

          if( response?.success === false ){
            setError(true);
          } else {
            setCharts(response.charts);
            setItems(response.items);
            setOverview(response.overview);
            setMetrics(response.metrics);
          }
        } catch (err) {
          setError(true);
        }
      };

      fetchData();
    }, []); // Empty dependency array ensures this runs only once

    if (error) {
      return (
        <NotFound />
      );
    }

    if (!charts && !error) {
      return (
        <Loading />
      );
    }

    return (
      <div className="container mx-auto mt-10 px-6">
        <h1 className="text-4xl font-bold text-gray-900 mb-10">{t('Dashboard')}</h1>
        <Banners />
        <Overview initialData={overview} />
        {Object.keys(charts).map((category) => {
          const categoryData = charts[category];
          const categoryLogs = metrics[category];

          if (! isNotEmpty( categoryData )) {
            return null;
          }

          return (
            <div key={category} className='overflow-hidden rounded-lg p-8 shadow-xl bg-white mt-10'>
              <div className="overflow-hidden">
              <div className="border-b border-gray-200 pb-5 sm:flex sm:items-center sm:justify-between">
                <h3 className="text-base font-semibold leading-6 text-gray-900">{t('Engagement Statistics')} {t('for')} {t(capitalizeFirstLetter(category))}</h3>
                <div className="mt-3 flex sm:ml-4 sm:mt-0">
                  <button
                    type="button"
                    onClick={() => navigate(`/history/${category}`)}
                    className="inline-flex items-center rounded-md bg-zinc-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-zinc-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-zinc-600"
                  >
                    {t('View Full History')}
                  </button>
                </div>
              </div>
                <div className="flex flex-wrap items-center">
                  <div className="w-full">
                    <Metrics initialData={categoryLogs} />
                  </div>
                  <div className="w-full">
                    <Charts initialData={categoryData} initialCategory={category} />
                  </div>
                </div>
              </div>
            </div>
          );
        })}
        {items && <Items initialData={items} />}
      </div>
    );
  };

  export default Main;