import React, { useState, useEffect } from 'react';
import { AgCharts } from 'ag-charts-react';
import { useTranslation } from 'react-i18next';
import { capitalizeFirstLetter } from '../../utils/functions';

const Charts = ({ initialData, initialCategory }) => {
  const [chartOptions, setChartOptions] = useState({});
  const { t } = useTranslation();

  useEffect(() => {
    const parsedData = (initialData && Array.isArray(initialData))
    ? initialData.sort((a, b) => new Date(a.date) - new Date(b.date))
    : [];

    setChartOptions({
      data: parsedData,
      series: [
        {
          type: 'line',
          xKey: 'date',
          yKey: 'total',
          yName: t(capitalizeFirstLetter(initialCategory)),
          strokeWidth: 3,
          marker: {
            enabled: true,
            size: 10,
          },
          interpolation: { type: "smooth" },
        },
      ],
      axes: [
        {
          type: 'category',
          position: 'bottom',
          title: {
            text: t('Dates'),
          },
          gridLine: {
            enabled: true,
            style: [
              {
                stroke: "rgba(219, 219, 219, 1)",
                lineDash: [4, 2],
              },
            ],
          },
        },
        {
          type: 'number',
          position: 'left',
          min: 0,
          nice: false,
          title: {
            text: t('Totals'),
          },
          label: {
            format: "d"
          },
          interval: { step: 10 },
          gridLine: {
            style: [
              {
                stroke: "rgba(219, 219, 219, 1)",
                lineDash: [4, 2],
              },
            ],
          },
        },
      ],
      legend: {
        position: 'top',
      },
      overlays: {
        noData: {
          text: t('No data to display'),
        },
      },
    });
  }, [initialData, initialCategory, t]);

  return (
    <div className='mb-5'>
      <AgCharts options={chartOptions} />
    </div>
  );
};

export default Charts;
