// CustomSelect.js
import React from 'react';
import Select from 'react-select';
import { useTranslation } from 'react-i18next';

const customSelectStyles = {
  container: (provided) => ({
    ...provided,
    width: '100%',
  }),
  control: (provided) => ({
    ...provided,
    minHeight: '2.5rem',
    transition: "none",
    borderRadius: '0.375rem',
    borderColor: '#d1d5db',
    boxShadow: '0 1px 2px rgba(0, 0, 0, 0.05)',
    '&:hover': {
      borderColor: '#f43f5e',
    },
  }),
  valueContainer: (provided) => ({
    ...provided,
    height: '100%',
    padding: '0 0.75rem',
  }),
  multiValueLabel: (base) => ({
    ...base,
    whiteSpace: "normal",
    overflow: "visible",
  }),
  input: (provided) => ({
    ...provided,
    margin: 0,
    "input:focus": {
      boxShadow: "none",
    },
  }),
  placeholder: (provided) => ({
    ...provided,
    margin: 0,
  }),
  singleValue: (provided) => ({
    ...provided,
    margin: 0,
  }),
};

const SelectField = ({ options, value, onChange, placeholder, isMulti = false, closeMenuOnSelect = false, hideSelectedOptions = false  }) => {
  const { t } = useTranslation();

  return (
    <Select
      styles={customSelectStyles}
      options={options}
      value={value}
      onChange={onChange}
      placeholder={t('Select...')}
      isMulti={isMulti}
      closeMenuOnSelect={closeMenuOnSelect}
      hideSelectedOptions={hideSelectedOptions}
      className="mt-1 block w-full"
    />
  );
};

export default SelectField;
