import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import { get } from './utils/services';

// Function to fetch translations from your API
async function fetchTranslations() {
  return await get('admin-ajax.php', { action: 'wp_ulike_localization' });
}

// Initialize i18next
i18n.use(initReactI18next).init({
  resources: {}, // Start with empty resources
  lng: 'en', // Default language
  fallbackLng: 'en',
  interpolation: {
    escapeValue: false, // React already escapes by default
  },
  initImmediate: false, // Prevent async loading issues
});

// Load translations and add them to i18next
async function loadTranslations() {
  const translations = await fetchTranslations();

  // Get the current language
  const currentLanguage = i18n.language || 'en'; // Default to 'en' if language is not set

  // Add or replace resources for the current language
  i18n.addResources(currentLanguage, 'translation', translations);

  // If the translations were added after the app started, you might need to reload the current language
  i18n.changeLanguage(currentLanguage);
}

// Load translations at startup
loadTranslations();

export default i18n;
