export const calculateGrowth = (current, previous) => {
    current  = Number(current);
    previous = Number(previous);

    if (previous === 0) {
        return current > 0 ? current : 0; // Use a maximum value for significant growth
    }
    return ((current - previous) / previous) * 100;
};

export const getGrowthColorClass = (growth) => {
    if (growth > 0) return 'text-green-500';
    if (growth < 0) return 'text-red-500';
    return 'text-gray-500'; // Neutral color for zero growth
};

export const isLocal = () => {
  return window.location.hostname === 'localhost' && window.location.port === '3000';
};

export const truncateText = (text, wordLimit) => {
  // Check if text is a string and not undefined
  if (typeof text !== 'string') {
    return '';
  }

  // Split the text into words
  const words = text.split(' ');

  // Check if the number of words exceeds the limit
  if (words.length > wordLimit) {
    // Join only up to the wordLimit number of words and add ellipsis
    return words.slice(0, wordLimit).join(' ') + '...';
  }

  // If the text is within the limit, return it as is
  return text;
};

export const isNotEmpty = (value) => {
  if (value == null) {
    return false;
  }
  if (typeof value === 'string') {
    return value.trim().length > 0;
  }
  if (Array.isArray(value)) {
    return value.length > 0;
  }
  if (typeof value === 'object') {
    return Object.keys(value).length > 0;
  }
  return false;
};

export function capitalizeFirstLetter(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

export function convertToCSV(json) {
  const array = typeof json !== 'object' ? JSON.parse(json) : json;
  const fields = array.length > 0 ? Object.keys(array[0]) : [];
  const csvRows = [];

  // Add headers
  csvRows.push(fields.join(','));

  // Add rows
  for (const item of array) {
    const row = fields.map(field => JSON.stringify(item[field], replacer)).join(',');
    csvRows.push(row);
  }

  return csvRows.join('\n');
};

// A replacer function to handle undefined or null values
export function replacer(key, value) {
  return value === null || value === undefined ? '' : value;
}

export function formatNumber(number) {
  const suffixes = ['', 'K', 'M', 'B', 'T']; // Suffixes for thousands, millions, billions, trillions
  if (number === 0) return '0';

  const tier = Math.floor(Math.log10(number) / 3); // Determine the tier
  const scaled = number / Math.pow(1000, tier);

  // Format the number with one decimal place if necessary
  const formattedNumber = scaled.toFixed(1).replace(/\.0$/, '');

  return `${formattedNumber}${suffixes[tier]}`;
}