import React, { useState } from 'react';
import { HandThumbUpIcon } from '@heroicons/react/24/solid'
import parse from 'html-react-parser';
import { capitalizeFirstLetter } from '../../utils/functions';
import { useTranslation } from 'react-i18next';
import SelectField from '../common/SelectField';

function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

const Items = ({ initialData }) => {
  const categories = Object.keys(initialData).filter(category => initialData[category].length > 0);
  const { t } = useTranslation();

  const [currentTab, setCurrentTab] = useState(categories[0]);

  const handleTabClick = (e, category) => {
    e.preventDefault();
    setCurrentTab(category);
  };

  // Create options for react-select
  const options = categories.map(category => ({
    value: category,
    label: `${t('Top')} ${t(capitalizeFirstLetter(category.replace(/\bpost\b/gi, "posts")
      .replace(/\bcomment\b/gi, "comments")
      .replace(/\bactivity\b/gi, "activities")
      .replace(/\btopic\b/gi, "topics")))}`,
  }));

  return (
    <div className='p-8 shadow-xl rounded-lg bg-white mt-10 mb-10'>
      <div className="overflow-hidden">

        <div>
          {/* Tab Navigation */}
          <div className="sm:hidden">
            <SelectField
              options={options}
              value={options.find(option => option.value === currentTab)}
              onChange={(selectedOption) => setCurrentTab(selectedOption.value)}
              closeMenuOnSelect={true}
              hideSelectedOptions={false}
            />
          </div>

          <div className="hidden sm:block">
            <div className="border-b border-gray-200">
              <nav className="-mb-px flex" aria-label="Tabs">
                {categories.map((category) => (
                  <button
                    key={category}
                    href="#"
                    onClick={(e) => handleTabClick(e, category)}
                    className={classNames(
                      category === currentTab ? 'border-rose-500 text-rose-600' : 'border-transparent text-gray-500 hover:border-gray-300 hover:text-gray-700',
                      'w-1/4 border-b-2 py-4 px-1 text-center text-sm font-medium'
                    )}
                    aria-current={category === currentTab ? 'page' : undefined}
                  >
                    {t('Top')} {t(capitalizeFirstLetter(category.replace(/\bpost\b/gi, "posts")
                    .replace(/\bcomment\b/gi, "comments")
                    .replace(/\bactivity\b/gi, "activities")
                    .replace(/\btopic\b/gi, "topics")))}
                  </button>
                ))}
              </nav>
            </div>
          </div>
        </div>

        <div className="space-y-2 p-4 mt-4">
          <ul className="divide-y divide-gray-100">
            {initialData[currentTab].map((item, index) => (
              <li
                key={index}
                className="flex flex-wrap items-center justify-between gap-x-6 gap-y-4 py-5 sm:flex-nowrap"
              >
                <div className="flex items-center gap-x-3">
                  <div className="min-w-0 list-decimal text-sm font-semibold leading-6 text-gray-900">
                    {++index}.
                  </div>
                  <div className="min-w-0 text-sm font-semibold leading-6 text-gray-900">
                    <a target='_blank' rel='noreferrer' href={parse(item.permalink)} className="hover:underline">
                      {parse(item.title)}
                    </a>
                  </div>
                </div>
                <dl className="flex w-full flex-none justify-between gap-x-8 sm:w-auto">
                  <div className="flex w-16 gap-x-2.5">
                    <dt>
                      <HandThumbUpIcon className="h-6 w-6 text-gray-400" aria-hidden="true" />
                    </dt>
                    <dd className="text-sm leading-6 text-gray-900">{item.likes_count}</dd>
                  </div>
                </dl>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Items;
