import React from 'react';
import { useTranslation } from 'react-i18next';
import { formatNumber } from '../../utils/functions';

const DataSection = ({ title, value }) => {

  return (
    <div className="flex flex-wrap items-baseline justify-between gap-x-2 gap-y-2 bg-white px-4 py-10 sm:px-6 xl:px-8">
      <dd className="w-full flex-none text-5xl font-extrabold	 leading-10 tracking-tight text-gray-900">
        {value !== undefined ? formatNumber(value) : 'No Data'}
      </dd>
      <dt className="text-sm font-normal leading-6 text-gray-500" dangerouslySetInnerHTML={{ __html: title }} />
    </div>
  );
};

const Metrics = ({ initialData }) => {
  const { t } = useTranslation();

  const sections = [
    { title: t('Engagement This Week'), value: initialData.week },
    { title: t('Monthly Engagement Overview'), value: initialData.month },
    { title: t('Yearly Engagement Trends'), value: initialData.year },
    { title: t('Overall Performance'), value: initialData.all }
  ];

  return (
    <dl className="mx-auto border-b border-r border-l grid grid-cols-1 gap-px bg-gray-900/5 sm:grid-cols-2 lg:grid-cols-4 mb-10">
      {sections.map((section, index) => (
        <DataSection
          key={index}
          title={section.title}
          value={section.value}
          previousValue={section?.previousValue}
        />
      ))}
    </dl>
  );
};

export default Metrics;
