import React from 'react';
import { Route, Routes } from 'react-router-dom';

let Main, Logs;

if (process.env.REACT_APP_BUILD_TYPE === 'pro') {
  Main = require('./pages/pro/Main').default;
  Logs = require('./pages/pro/Logs').default;
} else {
  Main = require('./pages/free/Main').default;
  Logs = require('./pages/free/Logs').default;
}

const App = () => (
  <Routes>
    <Route path="/" element={<Main />} />
    <Route path="/history/:category" element={<Logs />} />
  </Routes>
);

export default App;
