import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { get } from '../../utils/services';
import { truncateText, capitalizeFirstLetter } from '../../utils/functions';
import { useTranslation } from 'react-i18next';
import Pagination from '../../components/common/Pagination';
import NotFound from '../../components/common/NotFound';
import Loading from '../../components/common/Loading';
import { ArrowPathIcon, TrashIcon } from '@heroicons/react/24/solid';

const Logs = () => {
  const { category } = useParams();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [logs, setLogs] = useState(null);
  const [error, setError] = useState(false);
  const [page, setPage] = useState(1);
  const [deletingRowId, setDeletingRowId] = useState(null);
  const [loadingId, setLoadingId] = useState(null);
  const perPage = 20;

  const fetchLogs = useCallback(async (page) => {
    try {
      const response = await get('admin-ajax.php', {
        action: 'wp_ulike_history_api',
        type: category,
        page,
        perPage,
      });

      if (response?.success === false) {
        setError(true);
      } else {
        setLogs(response);
      }
    } catch (err) {
      setError(true);
    }
  }, [category]);

  useEffect(() => {
    fetchLogs(page);
  }, [category, page, fetchLogs]);

  if (error) {
    return <NotFound />;
  }

  if (!logs) {
    return <Loading />;
  }

  const totalPages = Math.ceil(logs.totalRecords / perPage);

  const handlePageChange = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
      setPage(newPage);
    }
  };

  const handleDelete = async (logId, type) => {
    try {
      setDeletingRowId(logId);
      setLoadingId(logId);
      const response = await get('admin-ajax.php', {
        action: 'wp_ulike_delete_history_api',
        id: logId,
        type
      });

      if (response.success) {
        setLogs(prevLogs => ({
          ...prevLogs,
          rows: prevLogs.rows.filter(log => log.id !== logId),
        }));
      } else {
        alert('Failed to delete the log entry.');
      }
    } catch (err) {
      console.error('Error deleting log entry:', err);
      alert('An error occurred while deleting the log entry.');
    } finally {
      setLoadingId(null);
    }
  };

  const commonHeaders = [
    t('Date'),
    t('User'),
    t('IP'),
    t('Status')
  ];

  const categoryHeaders = {
    comments: [
      t('Comment Author'),
      t('Comment Content')
    ],
    activities: [
      t('Activity Title')
    ],
    topics: [
      t('Topic Title')
    ],
    posts: [
      t('Post Title'),
      t('Category')
    ]
  };

  const getHeaders = () => {
    const additionalHeaders = categoryHeaders[category] || [];
    return [...commonHeaders, ...additionalHeaders, 'Actions'];
  };

  const renderTableHeaders = () => {
    const headers = getHeaders();
    const className = 'px-3 py-3.5 text-left rtl:text-right text-base font-semibold text-gray-900';

    return (
      <tr>
        {headers.map(header => (
          <th key={header} className={className}>
            {t(header)}
          </th>
        ))}
      </tr>
    );
  };

  const renderTableRows = () => {
    const className = 'px-3 py-4 text-sm text-gray-500';

    return logs.rows.map((log, index) => {
      const rowClasses = [
        deletingRowId === log.id ? 'bg-red-100 transition-opacity duration-300 opacity-0' : index % 2 === 0 ? 'bg-white' : 'bg-gray-50'
      ].join(' ');

      const commonCells = (
        <>
          <td className={className}>{log.date_time}</td>
          <td className={className} dangerouslySetInnerHTML={{ __html: log.user_id }} />
          <td className={className}>{log.ip}</td>
          <td className={className}>{log.status}</td>
        </>
      );

      const categoryCells = {
        comments: (
          <>
            {commonCells}
            <td className={className} dangerouslySetInnerHTML={{ __html: log.comment_author }} />
            <td className={className} dangerouslySetInnerHTML={{ __html: truncateText(log.comment_content, 15) }} />
          </>
        ),
        activities: (
          <>
            {commonCells}
            <td className={className} dangerouslySetInnerHTML={{ __html: truncateText(log.activity_title, 15) }} />
          </>
        ),
        topics: (
          <>
            {commonCells}
            <td className={className} dangerouslySetInnerHTML={{ __html: log.topic_title }} />
          </>
        ),
        posts: (
          <>
            {commonCells}
            <td className={className} dangerouslySetInnerHTML={{ __html: log.post_title }} />
            <td className={className} dangerouslySetInnerHTML={{ __html: log.category }} />
          </>
        )
      };

      return (
        <tr key={log.id} className={rowClasses}>
          {categoryCells[category] || commonCells}
          <td className={className}>
            <button
              type="button"
              onClick={() => handleDelete(log.id, category)}
              className={`inline-flex items-center rounded-md bg-white px-2.5 py-1.5 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 disabled:cursor-not-allowed disabled:opacity-30 disabled:hover:bg-white`}
              disabled={loadingId === log.id}
            >
              {loadingId === log.id ? (
                <ArrowPathIcon className="w-5 animate-spin" />
              ) : (
                <TrashIcon className="w-5" />
              )}
            </button>
          </td>
        </tr>
      );
    });
  };

  return (
    <div className="container mx-auto mt-10 px-6">
      <h1 className="text-4xl font-bold text-gray-900 mb-10">{t('Engagement History')}</h1>
      <div className="pb-5 sm:flex sm:items-center sm:justify-between">
        <div className="text-xl font-semibold leading-6 text-gray-900">{t(capitalizeFirstLetter(category))}</div>
        <div className="mt-3 flex sm:ml-4 sm:mt-0">
          <button
            type="button"
            onClick={() => navigate('/')}
            className="text-sm font-semibold leading-7 text-rose-600"
          >
            <span aria-hidden="true">&larr;</span> {t('Back')}
          </button>
        </div>
      </div>
      <div className="mt-4 flow-root">
        <div className="-mx-4 -my-2 overflow-x-auto sm:-mx-6 lg:-mx-8">
          <div className="inline-block min-w-full py-2 align-middle sm:px-6 lg:px-8">
            <div className="overflow-hidden shadow ring-1 ring-black ring-opacity-5 sm:rounded-lg">
              <table className="min-w-full divide-y divide-gray-300">
                <thead className="bg-gray-50">
                  {renderTableHeaders()}
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {renderTableRows()}
                </tbody>
              </table>
            </div>
            <div className="flex items-center justify-between mt-5 py-3 ">
                <div>
                  <p className="text-sm text-gray-700">
                    {t('Showing')} <span className="font-medium">{(page - 1) * perPage + 1}</span> {t('to')} <span className="font-medium">{Math.min(page * perPage, logs.totalRecords)}</span> {t('of')} <span className="font-medium">{logs.totalRecords}</span> {t('results')}
                  </p>
                </div>
                <Pagination
                  page={page}
                  totalPages={totalPages}
                  onPageChange={handlePageChange}
                />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Logs;
